.parent {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.login {
    min-width: 350px;
    min-height: 330px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #fff;
    border: 1px solid lightgray
}

.signup {
    margin-top: 20px;
    min-width: 350px;
    height: 63px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #fff;
    border: 1px solid lightgray
}

.username {
    width: 256px;
    height: 38px;
    margin: 5px auto 5px;
    color: black;
    padding: 0 6px;
    background-color: #FAFAFA;
    border: 1px solid lightgray;
    border-radius: 3px;

}

.password {
    width: 256px;
    height: 38px;
    margin: 0 auto 10px;
    color: black;
    padding: 0 6px;
    background-color: #FAFAFA;
    border: 1px solid lightgray;
    border-radius: 3px;
}

.loginBtn {
    background-color: #0095F6;
    border: none;
    border-radius: 3px;
    width: 258px;
    height: 38px;
    margin: 0 auto;
    color: white;
}


