.or {
    margin: 20px 0;
    font-size: 13px;
    color: #8e8e8e;
}

.facebook {
    color: #385185;
    font-size: 14px;
    text-decoration: none;
    font-weight: bold;
}

.forgot {
    margin-top: 25px;
    margin-bottom: 25px;
    color: #00376b;
    font-size: 12px;
}

.title {
    /* font-size: 36px; */
    padding: 22px 0 15px;
}

.title img {
    width: 150px;
    object-fit: contain;
}
.signup {
    display: flex;
    flex-direction: row;
    font-size: 14px;
}


.signupLink {
    margin-left: 5px;
    color: #0095f6;
    font-weight: bold;
}

.logo {
    background-repeat: no-repeat;
    background-position: 0 -130px;
    height: 51px;
    width: 175px;
    background-image: url(../../../Common/img/logos.png);
    display: block;
}