.aboutPost {
    width: 816px;
    height: 600px;
    display: flex;
    border: 1px solid lightgray;
    margin: 40px auto;
}
.imgWrapper {
    /* width: 60%; */
}

.img {
    height: 100%;
    width: 100%;
}

.headerWrapper {
    padding: 15px;
    display: flex;
    height: 72px;
    border-bottom: 1px solid lightgray;
    justify-content: space-between;
}

.avatar {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    border: 3px solid #DC316F;
    margin-right: 10px;
    cursor: pointer;
}

.header {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #262626;
    font-weight: 500;
}

.circles {
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}

.username {
    margin-right: 5px;
}

.follow {
    margin-bottom: 4px;
}

.mainInfo {
    padding: 15px;
    display: flex;
    height: 370px;
    border-bottom: 1px solid lightgray;
}

.avatarInfo{
    width: 30px;
    height: 30px;
    border-radius: 100%;
    border: 3px solid #DC316F;
    margin-right: 10px;
    cursor: pointer;
}

.infoWrapper {
    width: 40%;
    background-color: #fff;
}


.usernameInfo {
    font-size: 14px;
    margin-right: 5px;
    font-weight: 500;
}

.action {
    display: flex;
    align-items: center;
    padding: 13px 15px 0;
}

.like {
    font-size: 24px !important;
    margin-right: 12px;
    cursor: pointer;
}

.count {
    padding-left: 16px;
    padding-top: 5px;
    color: #262626;
    font-weight: 600;
    font-size: 14px;
}

.actionInfo {
    height: 100px;
}

.commentsForm {
    display: flex;
    min-height: 56px;
    border-top: 1px solid lightgray;
}

.comments {
    padding: 0 20px;
    font-size: 14px;
}

.allcomments {
    color: #8e8e8e;
    font-size: 14px;
    cursor: pointer;
    margin-bottom: 5px;
}

.btn {
    flex: 0;
    background-color: unset;
    border: none;
    color: #0095f6;
    font-weight: 600;
    font-size: 14px;
}

.input {
    flex: 1;
    border: 1px solid lightgray;
    border: 0;
    outline: none;
	-moz-appearance: none;
    padding: 5px 20px;
    overflow: auto;
    font-size: 14px;
}

.added {
    padding-left: 16px;
    padding-top: 10px;
    font-size: 12px;
    color: #8e8e8e;
}

.modalBtn {
    border: 0;
    border-bottom: 1px solid lightgray;
    background-color: #fff;
    padding: 10px;
    width: 300px;
    border-radius: 3px;
    font-size: 14px;
}
.red {
    color: red;
    font-weight: bold;
}