.messagesWrapper {
    display: flex;
    margin-top: 20px;
    border: 1px solid lightgray;
    border-radius: 4px;
}

.dialogs {
    width: 30%;
    background-color: #fff;
    min-height: 500px;
    border-right: 1px solid lightgray;
}

.messages {
    width: 70%;
    min-height: 500px;
    background-color: #fff;
}

.userName {
    text-align: center;
    padding: 20px;
    border-bottom: 1px solid lightgray;
}


.ava {
    width: 56px;
    height: 56px;
    border-radius: 100%;
    margin-right: 12px;
    cursor: pointer;
}

.dialogsPerson {
    display: flex;
    align-items: center;
    padding: 10px 0px 10px 20px ;
    color: black;
}
.dialogsPerson:hover {
    transition: .3s all;
    background-color: #FAFAFA;
    cursor: pointer;
}

.messagesHeader {
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
    padding: 20px;
    height: 65px;
}

/* .messages {
    height: 100%;
} */

.messagesAva {
    width: 24px;
    height: 24px;
    border-radius: 100%;
    margin-right: 12px;
    cursor: pointer;
}

.messagesForm {
    display: flex;
    align-items: flex-end;
    padding-bottom: 15px;
    flex: 1;
}

.input {
    width: 743px;
    height: 44px;
    border-radius: 22px;
    border: 1px solid lightgray;
    padding-left: 11px;
    padding-right: 8px;
    display: flex;
    justify-content: space-between;

}

.input input {
    border: 0;
    outline: none;
	-moz-appearance: none;
    flex: 1;
    border-radius: 10px;
}
.btn {
    flex: 0;
    background-color: unset;
    border: none;
    color: #0095f6;
    font-weight: 600;
    font-size: 14px;
}

.messageWrapper {
    height: 100%;
    margin-left: 20px;
}

.message {
    padding: 10px;
    margin-bottom: 10px;
    text-align: center;
    background-color: #EFEFEF;
    border-radius: 10px;
    display: inline-block;
}
.message:last-child {
    margin-bottom: 10px;
}
.date {
    font-size: 12px;
    color: #8e8e8e;
    text-align: center;
    padding: 10px 0;
}