.parent {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.login {
    width: 388px;
    min-height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #fff;
    border: 1px solid lightgray
}

.text {
    width: 300px;
    text-align: center;
    color: #8e8e8e;
    margin-top: 10px;
}

.s {
    font-size: 16px;
    font-weight: 600;
    color: #262626;
}
.signup {
    width: 388px;
    height: 63px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #FAFAFA;
    border: 1px solid lightgray
}

.username {
    width: 286px;
    height: 38px;
    margin: 10px auto 10px;
    color: black;
    padding: 0 6px;
    background-color: #FAFAFA;
    border: 1px solid lightgray;
    border-radius: 3px;
}

.password {
    width: 256px;
    height: 34px;
    margin: 0 auto 10px;
    color: black;
    padding: 0 6px;
    background-color: #FAFAFA;
    border: 1px solid lightgray;
    border-radius: 3px;
}

.loginBtn {
    background-color: #0095F6;
    border: none;
    border-radius: 3px;
    width: 300px;
    height: 30px;
    margin: 0 auto;
    color: white;
    font-weight: 600;
}


