.or {
    margin: 20px 0;
    font-size: 13px;
    color: #8e8e8e;
    font-weight: 500;
}



.title {
    /* font-size: 36px; */
    padding: 22px 0 15px;
}

.title img {
    width: 150px;
    object-fit: contain;
}
.signup {
    display: flex;
    flex-direction: row;
    font-size: 14px;
}


.signupLink {
    margin-left: 5px;
    color: #262626;
    font-weight: bold;
}


.logo {
    background-position: -130px 0;
    background-image: url(../../../Common/img/logos.png);
    height: 96px;
    width: 96px;
    background-repeat: no-repeat;
    display: block;
}

.facebook {
    color: black;
    font-weight: 500;
    font-size: 14px;
}