.profile {
    display: flex;
    padding: 30px 100px;
}


.ava {
    width: 150px;
    height: 150px;
    border-radius: 100%;
    object-fit: cover;
    margin-right: 100px;
}

.username {
    font-size: 28px;
    color: #262626;
    margin-right: 20px;
}

.settings {
    border: none;
    background-color: inherit;
    font-size: 25px;
}

.edit {
    min-width: 180px;
    min-height: 30px;
    border: 1px solid lightgray;
    background-color: #FAFAFA;
    font-size: 14px;
    color: #262626;
    font-weight: 600;
}