.logo {
    object-fit: contain;
}



.header {
    background-color: #FFFFFF;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    position: sticky;
    top: 0;
    z-index: 100;  
}

.icons {
    display: flex;
    justify-content: space-between;
}

.icon {
    margin-right: 15px;
    font-size: 22px;
    cursor: pointer;
}

.iconLink {
    color: black;
    margin-right: 15px;
    text-decoration: none;
}

.icon:last-child {
    margin-right: 0;
}

.input {
    width: 215px;
    height: 28px;
    border-radius: 4px;
    border: 1px solid lightgray;
    padding: 5px;
}


.input::placeholder {
    text-align: center;
}


/* .wrapper {
    width: 360px;
} */


.logout {
    border: 1px solid lightgray;
    border-radius: 4px;
    background-color: #0095F6;
    color: white;
}

.headerWrapper {
    margin: 0 auto;
    background-color: white;
    border-bottom: 1px solid lightgray;
}


