.postImg {
    width: 100%;
    object-fit: contain;
}

.postText {
    font-weight: normal;
    padding: 5px 20px 0;
}

.avatar {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    border: 3px solid #DC316F;
    margin-right: 12px;
    cursor: pointer;
}

.header {
    display: flex;
    align-items: center;
    padding: 12px;
    justify-content: space-between;
}

.nickname {
    font-weight: 600;
    cursor: pointer;
    color: #262626;
    font-size: 14px;
}

.post {
    max-width: 614px;
    display: flex;
    justify-content: center;
    align-content: center;
    margin: 25px auto;
    border: 1px solid lightgray;
    background-color: #fff;
}

.name {
    display: flex;
    align-items: center;
}

.circles {
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}


.input {
    flex: 1;
    border: 1px solid lightgray;
    border: 0;
    outline: none;
	-moz-appearance: none;
    padding: 5px 20px;
    overflow: auto;
    font-size: 14px;
}

.btn {
    flex: 0;
    background-color: unset;
    border: none;
    color: #0095f6;
    font-weight: 600;
    font-size: 14px;
}

.commentsForm {
    display: flex;
    min-height: 56px;
    border-top: 1px solid lightgray;
}

.comments {
    padding: 0 20px;
    font-size: 14px;
}

.allcomments {
    color: #8e8e8e;
    font-size: 14px;
    cursor: pointer;
    margin-bottom: 5px;
}

.modalBtn {
    border: 0;
    border-bottom: 1px solid lightgray;
    background-color: #fff;
    padding: 10px;
    width: 300px;
    border-radius: 3px;
    font-size: 14px;
}


.red {
    color: red;
    font-weight: bold;
}

.action {
    display: flex;
    align-items: center;
    padding: 5px 19px 0;
}

.like {
    font-size: 24px !important;
    margin-right: 12px;
    cursor: pointer;
}

.count {
    padding-left: 21px;
    color: #262626;
    font-weight: 600;
    font-size: 14px;
}