.upload {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 500px;
    margin: 0 auto; 
    background-color: #fff;  
}

.input {
    border-radius: 4px;
    border: 1px solid lightgray;
    width: 400px;
    height: 300px;
    padding: 5px;
    margin-top: 10px;
}

.file {
    margin-top: 20px;
}

.progress {
    width: 300px;
    height: 20px;
}

.uploadPost {
    text-align: center;
    margin-top: 15px;
}